@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
.App {
  width: 100vw;
  color: #212121;
  font-family: "Open Sans", sans-serif;
  text-align: center;
}
body {
  padding: 0%;
  margin: 0%;
  background: #eefbfb;
  font-family: Arial, Helvetica, sans-serif;
}

nav {
  margin: 0;
  width: 100%;
  height: 100px;
  background-color: black;
  display: flex;
  /*position: fixed;*/
  /*top: 0;*/
  /*overflow: auto;*/

  /*justify-content: center;*/
  align-items: center;
  color: white;
  text-decoration: none;
  justify-content: space-between;
}

a {
  color: white;
  text-decoration: none;
  margin: 10px;
  font-size: 25px;
}

.hide {
  opacity: 0 !important;
}

/*.rightnav {*/
/*  position: fixed;*/
/*  right: 20px;*/
/*}*/

.right-nav a {
  font-size: medium;
}

.logout-btn {
  background-color: black;
}

.chat-window {
  width: 300px;
  height: 420px;
}

.chat-window p {
  margin: 0;
}
.chat-window .chat-header {
  height: 45px;
  border-radius: 6px;
  background: #263238;
  position: relative;
  cursor: pointer;
}
.chat-window .chat-header p {
  display: block;
  padding: 0 1em 0 2em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
}

.chat-window .chat-body {
  height: calc(450px - (45px + 70px));
  border: 1px solid #263238;
  background: #fff;

  position: relative;
}
.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}
.chat-window .chat-body .message {
  height: auto;
  padding: 10px;
  display: flex;
}

.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  min-height: 40px;
  max-width: 120px;
  background-color: #43a047;
  border-radius: 5px;
  color: white;
  display: flex;
  align-items: center;
  margin-right: 5px;
  margin-left: 5px;
  padding-right: 5px;
  padding-left: 5px;
  overflow-wrap: break-word;
  word-break: break-word;
}

.chat-window .chat-body .message .message-meta {
  display: flex;
  font-size: 12px;
}

.chat-window .chat-footer {
  height: 40px;
  border: 1px solid #263238;
  background-color: white;
  border-top: none;
  display: flex;
  align-items: center;
}

.chat-window .chat-footer input {
  height: 100%;
  flex: 85% 1;
  padding: 0 0.7em;
  font-size: 1em;
  border-right: 1px dotted #607d8b;
  outline: none;
  font-family: "Open Sans", sans-serif;
}

.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15% 1;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 25px;
  color: lightskyblue;
}

.chat-window .chat-footer button:hover {
  color: #43a047;
}

#you {
  justify-content: flex-start;
}

#you .message-content {
  justify-content: flex-start;
}

#you .message-meta {
  justify-content: flex-start;
  margin-left: 5px;
}

#other {
  justify-content: flex-end;
}

#other .message-content {
  justify-content: flex-end;
  background-color: cornflowerblue;
}

#other .message-meta {
  justify-content: flex-end;
  margin-right: 5px;
}

.message-meta #author {
  margin-left: 10px;
  font-weight: bold;
}

.joinChatContainer {
  display: flex;
  flex-direction: column;
  background: grey;
  border-radius: 10px;
  /* text-align: center; */
}

.joinChatContainer h3 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.joinChatContainer input {
  width: 210px;
  height: 40px;
  margin: 7px;
  border: 2px solid #43a047;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
}

.joinChatContainer select {
  width: 210px;
  height: 40px;
  margin: 7px;
  border: 2px solid #43a047;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
}

.joinChatContainer button {
  width: 225px;
  height: 50px;
  margin: 7px;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  background: #43a047;
  color: #fff;
  cursor: pointer;
}

.joinChatContainer button:hover {
  background: #2e7d32;
}

.chat-container {
  position: fixed;
  top: 80px;
  right: 50px;
}

.crypto-table {
  margin: auto;
  margin-top: 80px;
  text-align: center;
  display: flex;
  justify-content: center;
}
.crypto-table table {
  border-collapse: collapse;
  overflow-x: scroll;
}
.crypto-table thead {
  background: pink;
}
.crypto-table table th:first-child {
  border-radius: 10px 0 0 10px;
}

.crypto-table table th:last-child {
  border-radius: 0 10px 10px 0;
}

.crypto-table thead th {
  padding: 10px 5px;
}

.crypto-table tbody td {
  padding: 5px 5px;
}

.buy-button {
  background-color: seagreen;
}

.chat-toggle {
  position: fixed;
  top: 80px;
  right: 50px;
  z-index: 10;
  background-color: grey;
}

.modalBackground {
  width: 100vw;
  height: 100vh;
  background-color: rgba(200, 200, 200);
  position: fixed;
  left: 0px;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer {
  width: 500px;
  height: 500px;
  border-radius: 12px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  display: flex;
  flex-direction: column;
  padding: 25px;
}

.modalContainer .title {
  display: inline-block;
  text-align: center;
  margin-top: 10px;
}

.titleCloseBtn {
  display: flex;
  justify-content: flex-end;
}

.titleCloseBtn button {
  background-color: transparent;
  border: none;
  font-size: 25px;
  cursor: pointer;
}

.modalContainer .body {
  flex: 50% 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.7rem;
  text-align: center;
}
.transaction-input {
  padding: 20px;
  margin: 10px;
  width: 70%;
}

.modalContainer .footer {
  flex: 20% 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer .footer button {
  width: 150px;
  height: 45px;
  margin: 10px;
  border: none;
  background-color: cornflowerblue;
  color: white;
  border-radius: 8px;
  font-size: 20px;
  cursor: pointer;
}

#cancelBtn {
  background-color: crimson;
}

.portfolio-table {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: "Nunito", sans-serif;
}

.portfolio-table .openModalBtn {
  width: 100px;
  height: 40px;
  border: none;
  margin: 10px 5px;
  border-radius: 6px;
  color: white;
  cursor: pointer;
}

.buy {
  background-color: green;
}
.sell {
  background-color: red;
}

.loginPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 18px;
}

.input-fields {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

input {
  width: 90%;
  height: 20px;
  background: #e0dede;
  margin: 10px;
  padding: 10px;
  border: none;
  border-radius: 8px;
}

button {
  padding: 0.3em 1.2em;
  margin: 0 0.1em 0.1em 0;
  border: 0.16em solid rgba(255, 255, 255, 0);
  border-radius: 2em;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #ffffff;
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  text-align: center;
  transition: all 0.2s;
}

.sign-in {
  background-color: #4e9af1;
}
.google-sign-in {
  background-color: #d9544f;
}

.register {
  font-size: medium;
}
.register a {
  color: #4e9af1;
  font-size: medium;
}

.registration-submit {
  background-color: blue;
}

.balance_input {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  margin: 10px auto;
}
.balance_input label {
  font-size: xx-large;
}

.balance_input input {
  width: 80%;
}

.balance_input select {
  width: 80%;
  margin: 20px;
  border-radius: 10px;
}

.balance_input button {
  background-color: black;
  width: 60%;
  margin-top: 10px;
}

